#map {
    width: 100%;
    height: 100%;
}

body {
    margin:0;
    padding:0;
    font-family:Arial;
}

.page {
    display: grid;

    grid-template-areas:
            "header header header"
            "left content content";

    grid-template-columns: 200px 1fr 200px;
    grid-template-rows: auto 1fr auto;
    grid-gap: 0px;

    height: 100vh;

}

.header {
    grid-area: header;
    background: black;
    text-transform: uppercase;
    color: white;
    padding:0.5rem;
    font-size: 20px;
}

.left-column {
    grid-area: left;
    padding: 0.5rem;
    font-size: 16px;
    display: flex;
    flex-direction: column;
    position: relative;
}

.right-column {
    grid-area: content;
}


@media (max-width: 768px) {
    .page {
        grid-template-areas:
                "header"
                "left"
                "content";

        grid-template-columns: 1fr;
        grid-template-rows:
                auto /* Header */
                minmax(75px, auto) /* left */
                1fr /* Content */;
    }

    .left-column {
        margin: 0;
    }
}

.marker {
    background-size: cover;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
}



#cafeCard{
    position: absolute;
    top: 0;
    bottom: 0;
    background: lavender;
    .closeBtn{
        float: right;
        color:coral;
    }
}

.sidebar{
    display: flex;
    flex-direction: column;
    align-items: stretch;
    flex-grow: 1;

    ul.scrollable{
        position: absolute;
        top: 1.5em;
        bottom: 0;
        left:0.5em;
        right:0;
        display: block;
        overflow-y: scroll;
        padding-left: 0;
    }
}


.filterOpenNow,
.filterEcoFriendly {
    border-radius: 10px;
    display: inline;
    background: rgba(0,0,0,0.2);
    padding: 2px 4px;
    font-size: 12px;
    cursor: pointer;

    &.__checked {
        background: rgba(0,0,0,0.5)
    }
}

.filterOpenNow {
    margin-left: 5px;
}

.filterAverageCheck__slider {
    position: relative;
    height: 30px;
}

.filterAverageCheck__from,
.filterAverageCheck__to {
    position: absolute;
    top: 50%;
}

.noUi-target {
    box-shadow: none;
    background: none;
    height: 0;
    top: 50%;
}

.noUi-handle {
    top: -3px !important;
    right: -3px !important;
    width: 6px !important;
    height: 6px !important;
    cursor: pointer;
    background: black;
    border-radius: 10px;
    box-shadow: none;

    &::before {
        top: -4px;
        left: -4px;
        width: 12px !important;
        height: 12px !important;
        background: rgba(0, 0, 0, 0.5);
        border-radius: 20px;
    }

    &::after {
        display: none;
    }

    &:focus {
        outline: none;
    }
}

.noUi-connects,
.noUi-connect,
.noUi-pips,
.noUi-marker {
    display: none;
}
